<template>
    <div class="clock">
        {{hours}}:{{minutes}}:{{seconds}}
    </div>
</template>

<script>
    export default {
        data() {
            return {
                hours: "00",
                minutes: "00",
                seconds: "00",

                hoursAsNumber: 0,
                minutesAsNumber: 0,
                secondsAsNumber: 0,
            }
        },
        methods: {
            increaseSecounds() {
                this.secondsAsNumber += 1;
                if (this.secondsAsNumber === 60) {
                    this.secondsAsNumber = 0;
                    this.minutesAsNumber += 1;
                }
                if (this.minutesAsNumber === 60) {
                    this.minutesAsNumber = 0;
                    this.hoursAsNumber += 1;
                }
                if (this.hoursAsNumber === 24) {
                    this.hoursAsNumber = 0;
                    this.secondsAsNumber += 1;
                }

                this.buildClock();
            },
            buildClock() {
                this.hours = this.hoursAsNumber < 10 ? "0" + this.hoursAsNumber : "" + this.hoursAsNumber;
                this.minutes = this.minutesAsNumber < 10 ? "0" + this.minutesAsNumber : "" + this.minutesAsNumber;
                this.seconds = this.secondsAsNumber < 10 ? "0" + this.secondsAsNumber : "" + this.secondsAsNumber;
            }
        },
        created() {
            let today = new Date();
            this.hoursAsNumber = today.getHours();
            this.minutesAsNumber = today.getMinutes();
            this.secondsAsNumber = today.getSeconds();
            this.buildClock();

            this.$options.interval = setInterval(this.increaseSecounds, 1000)
        },
        beforeDestroy() {
            clearInterval(this.$options.interval)
        }

    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .clock {
        font-size: 14px;
        font-weight: 500;
        margin: 6px;
    }
</style>